const siteLights = document.getElementById("devices");
const siteHeader = document.getElementById("header");
const siteStatistics = document.getElementById("statistics");
const btnLights = document.querySelectorAll(".light");
const btnHeader = document.querySelectorAll(".header");
const btnStat = document.querySelectorAll(".statistics");

import { loadDeviceList } from './dev_click';

export const changeSite = () => {

    btnStat.forEach((el) => {
        console.log(el);
    
        el.addEventListener("click",function(){
            siteLights.style.display="none";
            siteHeader.style.display="none";
            siteStatistics.style.display="block";
        })
    }
    );

    btnLights.forEach((el) => {
        console.log(el);
        const deviceListElem = document.querySelector("#device-list")

        el.addEventListener("click",function(){
            siteLights.style.display="block";
            siteHeader.style.display="none";
            siteStatistics.style.display="none";

            //deviceListElem.innerHTML = 'Ładowanie listy urządzeń...'
            //loadDeviceList()
        })
    }
    );

    btnHeader.forEach((el) => {
        console.log(el);
    
        el.addEventListener("click",function(){
            siteLights.style.display="none";
            siteHeader.style.display="block";
            siteStatistics.style.display="none";
        })
    }
    );
    
};