const send = document.getElementById('addDev');
const olDev = document.getElementById('device-list');
const valueDev = document.getElementById('selectValue');


const box = document.getElementById('box');
 var i = 0;
export const addDev = () => {

send.addEventListener("click", function () {

    console.log(olDev);

    if(valueDev.value==='zarowka')

    olDev.innerHTML+= `<li class="dev__item" id="btn`+i+`" onclick="this.nextSibling.nextSibling.style.display='block'">`+valueDev.value+`
    </li>

        <div id="item`+i+`" class="dev__item--1 bg">
            <form action="">
                
                <img class="lamp" alt="image" src="../../images/lamp-off.svg" display="block">
        
                <a  id="lamp" class="btn btn--form" data-state="0" onclick="if (this.getAttribute('data-state') == '0') {this.setAttribute('data-state', '1'); this.innerHTML='Wyłącz';this.previousSibling.previousSibling.src='../../images/lamp-on.svg'; } else {this.setAttribute('data-state', '0'); this.innerHTML='Włącz'; this.previousSibling.previousSibling.src='../../images/lamp-off.svg'; }">Wyłącz</a>
                
            </form>
        </div>`;

        if(valueDev.value==='piec')

    olDev.innerHTML+= `<li class="dev__item" id="btn--bake`+i+`" onclick="this.nextSibling.nextSibling.style.display='block'">Piec</li>
    <div id="item`+i+`" class="dev__item--2 bg">
            

        <form action="">
            <h2>Ustaw temperature</h2>
            <input type="number" name="" id="bake--input`+i+`">
            <a  class="btn btn--form" id="btn--bake--send">Zatwierdź</a>
            <div id="send`+i+`"></div>
        </form>
      
    </div>`

    if(valueDev.value==='grzejnik')
   olDev.innerHTML+=
    `<li class="dev__item" id="btn--heater`+i+`" onclick="this.nextSibling.nextSibling.style.display='block'">Grzejnik</li>
    <div id="item`+i+`" class="dev__item--3 bg">
            

            <form action="">
                <h2>Ustaw temperature</h2>
                <input type="number" name=""id="heat--input">
                <a  class="btn btn--form" id="btn--heater--send">Zatwierdź</a>
                <div id="send`+i+`"></div>
            </form>
        </div>`

   

});

i++;

}

    
