export const devClickLight = () => {
    let i=0;
btnlight.addEventListener('click', function(){
    if(i==1) {
        item1.style.display="none";

    }
    else {
       
        item1.style.display="block";
    }
    
    i++;

    if(i>1) i=0;
    console.log(i);
})
};

