function nodizeDeviceElemForm(type, state, elem) {
    if (type == 'Przełącznik światła') {
        const imageElem = document.createElement('img')
        imageElem.className = 'lamp'
        imageElem.src = (state > 0.5) ? '../../images/lamp-on.svg' : '../../images/lamp-off.svg'

        const anchorElem = document.createElement('a')
        anchorElem.className = 'btn btn--form'
        anchorElem.innerHTML = (state > 0.5) ? 'Wyłącz' : 'Włącz'
        anchorElem.addEventListener('click', (e) => {
            const effectorId = elem.previousSibling.getAttribute('data-effector-id')
            const putData = {
                state: (state > 0.5) ? 0.0 : 1.0
            }

            // TODO: PUT '/efektory/' + effectorId
            setTimeout(() => {
                // when successfull
                state = putData.state
                anchorElem.innerHTML = (state > 0.5) ? 'Wyłącz' : 'Włącz'
                imageElem.src = (state > 0.5) ? '../../images/lamp-on.svg' : '../../images/lamp-off.svg'
            }, 1000)
        })

        elem.appendChild(imageElem)
        elem.appendChild(anchorElem)
    } else if (type == 'Regulator temperatury') {
        const headingElem = document.createElement('h2')
        headingElem.innerHTML = 'Ustaw temperaturę'

        const inputElem = document.createElement('input')
        inputElem.type = 'number'
        inputElem.step = '0.01'
        inputElem.value = Number(state)

        const anchorElem = document.createElement('a')
        anchorElem.className = 'btn btn--form'
        anchorElem.innerHTML = 'Zmień'
        anchorElem.addEventListener('click', (e) => {
            const effectorId = elem.previousSibling.getAttribute('data-effector-id')
            const putData = {
                state: inputElem.value
            }

            // TODO: PUT '/efektory/' + effectorId
            setTimeout(() => {
                // when successfull

                console.log('Temperatura efektora ' + effectorId + '; -> ' + putData.state)
            }, 1000)
        })

        elem.appendChild(headingElem)
        elem.appendChild(inputElem)
        elem.appendChild(anchorElem)
    } else {
        elem.innerHTML = 'Nieznany typ urządzenia.'
    }
}

export const loadDeviceList = () => {
    const deviceListElem = document.querySelector("#device-list")

    setTimeout(function () {
        // TODO: GET '/efektory'
        const dataMock = [
            {
                "id_efektora": 1,
                "nazwa": "Piec"
            },
            {
                "id_efektora": 2,
                "nazwa": "Żarówa"
            }
        ]

        deviceListElem.innerHTML = ''

        for (const device of dataMock) {
            const deviceListEntryElem = document.createElement('li')
            deviceListEntryElem.className = 'dev__item'
            deviceListEntryElem.innerHTML = device.nazwa
            deviceListEntryElem.setAttribute('data-effector-id', device.id_efektora)
            deviceListEntryElem.addEventListener('click', (e) => {
                const listElem = e.target
                const effectorId = Number(listElem.getAttribute('data-effector-id'))
                const deviceElem = document.createElement('div')

                // TODO: GET '/efektory/' + effectorId
                const dataDeviceMock = {
                    "nazwa": "Piec",
                    "typ": "Regulator temperatury",
                    "stan": 33.78
                }

                nodizeDeviceElemForm(dataDeviceMock.typ, dataDeviceMock.stan, deviceElem)

                listElem.parentNode.insertBefore(deviceElem, listElem.nextSibling)
            })

            deviceListElem.appendChild(deviceListEntryElem)
        }


    }, 1000)
}
