const btnczujnik = document.querySelector("#btnczujnik");
const btnurzadzenia = document.querySelector("#btnurzadzenia");
const diagCzujnik = document.querySelector("#diagramCzujnik");
const diagUrzadzenia = document.querySelector("#diagramUrzadzenia");

export const czujnikClick = () => {


    let i=0;
    btnczujnik.addEventListener('click', function(){
        console.log("click");
        if(i==1) {
            diagCzujnik.style.display="none";
    
        }
        else {
           
            diagCzujnik.style.display="block";
        }
        
        i++;
    
        if(i>1) i=0;

    })
    };

    export const urzadzeniaClick = () => {


        let i=0;
        btnurzadzenia.addEventListener('click', function(){
            console.log("click");
            if(i==1) {
                diagUrzadzenia.style.display="none";
        
            }
            else {
               
                diagUrzadzenia.style.display="block";
            }
            
            i++;
        
            if(i>1) i=0;
    
        })
        };

